<template>

  <div class="common-info">
    <div v-if="dataItem.title" class="title title-page">
      <div class="title-left">
        <h2>{{ dataItem.title }}</h2>
      </div>
    </div>

    <div
      v-for="(items, indexs) in dataItem.data"
      :key="indexs"
      class="common-main box"
    >
      <div
        v-for="(dxItems, dxIndexs) in items"
        :key="dxIndexs"
        class="page-list"
      >
        <div v-if="dxItems.type!==0" class="pagelist-left">{{ dxItems.name }}</div>
        <div v-if="dxItems.type === 1" class="pagelist-right">{{ dxItems.value }}</div>
        <div v-else-if="dxItems.type === 2" class="pagelist-right">{{ getTime (dxItems.value) }}</div>

        <div v-else-if="dxItems.type === 9 || dxItems.type === 10 || dxItems.type === 11 || dxItems.type === 12 " class="pagelist-right  ">
          <div v-if="dxItems.files !== ''&& dxItems.files !== null ">
            <ul
              v-for="(fjItem, fjIndex) in dxItems.values"
              :key="fjIndex"
            >
              <li @click="loadFiles(fjItem,dxItems.files[indexs])">
                <i class="iconfont iconxiazai" />
                {{ fjItem }}
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <!-- 更多 end -->
    <div v-if="dataItem.totalpageno>1 && showMore" class="page-more" @click="goMore(dataItem)">
      查看更多信息
      <i class="iconfont iconarrowright" />
    </div>
  </div>

</template>

<script>
import { NativeJS } from '@/common/js/mixin.js'
import { formatTimes } from '@/common/js/util'

export default {
  components: {

  },
  mixins: [NativeJS],
  /**
     * 表单 {id:"",title:"",type:"",data[]}
     * type: 1-单表 2-列表
     * 字段{name:"",value:"",type:"",files:[],values:[]}
     * type:1-text 9-file 10-pdf 11-img 12-html
     *
     *[{"id":null,"title":"推荐供应商","type":2,"data":[[
       {"name":"供应商名称","value":"9151010009971167XW","type":1,"file":null,"values":null,"files":null},
       ]
       ],"totalpageno":0,"showmore":"app/xm/getLxgys/50a7c716a3ec4ecebe7eae69d66f3091","sqdDataList":[]}]
     */
  props: {
    dataItem: { // 数据源
      type: null,
      required: true
    },
    showMore: { // 是否展示更多
      type: null,
      required: true
    }
  },
  data() {
    return {

    }
  },

  watch: {

  },

  created() {
  },

  mounted() {
  },

  methods: {
    getTime(kbsj) {
      var time = formatTimes(kbsj)
      return time
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)

      this.NativeDownFiles(url)
    },
    goMore(item) {
      this.$emit('goMoreAction', item)
    }
  }
}
</script>

