<template>
  <div>
    <div class="container-mini">
      <div class="content">
        <div class="common-main box">
          <!-- <div class="title title-page">
            <div class="title-left">
              <h2>{{ jdxxList.title }}</h2>
            </div>
          </div> -->

          <!-- <div v-for="(item,index) in xmxxList" :key="index">
            <div class="page-list">
              <div class="pagelist-left">{{ item.name }}</div>
              <div class="pagelist-right">{{ item.value }}</div>
            </div>
          </div> -->

          <div
            v-for="(item,index) in jdxxList"
            :key="index"
          >

            <div v-if="item.ggnrStr" class="content" v-html="item.ggnrStr" />

            <div v-else>
              <mouldsList v-if="sfdlbb ===1" :data-item="item" :show-more="showMore" @goMoreAction="MoreAction($event)" />
              <mouldList v-if="sfdlbb ===0" :data-item="item" :show-more="showMore" @goMoreAction="MoreAction($event)" />
            </div>

          </div>

        </div>
      </div>

    </div>
  </div>

</template>

<script land="script" scoped>
import { NativeJS } from '@/common/js/mixin.js'
import mouldList from '@/components/list/mouldList'
import mouldsList from '@/components/list/mouldsList'
import { mapState } from 'vuex'

export default {
  name: 'Jdxq',
  components: {
    mouldList,
    mouldsList
  },
  mixins: [NativeJS],
  props: {
    appsrvurl: {
      type: null,
      default: ''
    },
    xmid: {
      type: null,
      default: ''
    },
    sfdlbb: {
      type: null,
      default: ''
    }
  },
  data() {
    return {
      jdxxList: [],
      showMore: false
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    console.log('jdxxList---------', this.jdxxList)
    console.log('sfdlbb---------', this.sfdlbb)
    this.getJdxx()
  },
  methods: {
    getJdxx() {
      this.$store.dispatch('gyswdxm/getGysJdxqAction', { xmid: this.xmid, zzid: this.userInfo.zzid, appsrvurl: this.appsrvurl }).then(res => {
        this.jdxxList = res
        console.log('jdxxList----', this.jdxxList)
      }).catch((error) => {
        this.$toast(error)
      })
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)
      this.NativeDownFiles(url)
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }
  }

}
</script>
