<template>
  <div>
    <van-sticky z-index="100">
      <headerBar :header-data="headerData" />
      <!-- 项目信息 begin -->
      <div class="fixed-project">
        <div class="list">
          <h3><div class="tit">{{ xmmc }}</div></h3>
        </div>
      </div>
      <!-- 项目信息 end -->
      <!-- 操作进度 begin -->
      <div class="scrolltab scrolltab-progress">
        <ul ref="tabWrapper" class="tab_content">
          <!--<ul class="tab_content flex" ref="tabWrapper">-->
          <li v-for="(itemss ,indexss) in phaseTaskList" :key="indexss" :class="getClass(itemss.status,indexss)">
            <span class="tit" @click.stop="getGysLcxd(itemss.name,itemss.id,itemss.status,indexss)">{{ itemss.name }}</span>
          </li>
        </ul>
      </div>
    <!-- 操作进度 end -->
    </van-sticky>
    <div v-if="appsrvurl!==''">
      <ggxx v-if="name ==='公告查看'" ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <bj v-else-if="name === '在线报价' " ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <zcbj v-else-if="name === '再次报价' " ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <xmcq v-else-if="name === '项目澄清' " ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <Yqhqr v-else-if="name === '邀请函确认' " ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <Jggg v-else-if="name ==='结果公告'" ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <Jggs v-else-if="name ==='结果公示'" ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <zbjg v-else-if="name ==='中标结果'" ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <Xmtb v-else-if="name ==='投标'" ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
      <Jdxq v-else ref="mychild" :xmid="xmid" :appsrvurl="appsrvurl" :sfdlbb="sfdlbb" />
    </div>
    <!-- <div v-for="(item1 ,index1) in tabs" :key="index1"> -->
    <!-- <xmxx v-if="lcindex == 0 " ref="mychild" :xmid="xmid" />
    <ggxx v-if="lcindex == 1 " ref="mychild" :xmid="xmid" />
    <xmcq v-if="lcindex == 2 " ref="mychild" :xmid="xmid" /> -->
    <!-- </div> -->
    <!-- <FooterNav /> -->
  </div>
</template>

<script>
import headerBar from '@/components/header/headerBar'
// import FooterNav from '@/components/footerNav/footerNav.vue'
// import xmxx from './xm/Xmxx'
import ggxx from './xm/Ggxx'
import bj from './bj/WdxmBj'
import zcbj from './bj/WdxmZcbj'
import xmcq from './xm/Xmcq'
import Jdxq from './xm/Jdxq'
import Yqhqr from './xm/Yqhqr'
import Jggg from './xm/XmJggg'
import Jggs from './xm/XmJggs'
import zbjg from './xm/Zbjg'
import Xmtb from './xm/Xmtb'
import { pageRouter } from '@/common/js/mixin.js'
import { mapState } from 'vuex'

export default {
  name: 'Wdxmxmxq',
  components: {
    headerBar,
    bj,
    zcbj,
    ggxx,
    xmcq,
    Jdxq,
    Yqhqr,
    Jggg,
    Jggs,
    zbjg,
    Xmtb
    // [InputItem.name]: InputItem
  },
  mixins: [pageRouter],
  data() {
    return {
      selectIndex: '',
      xmmc: '',
      appsrvurl: '',
      sfdlbb: '',
      name: '',
      lcindex: 0,
      phaseTaskList: [],
      curDataTabList: [],
      xmid: '',
      bmgysid: '',
      headerData: {
        title: '项目信息',
        leftUrl: '/Wdxm',
        leftText: ' '
      }
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.xmid = this.$store.state.gyswdxm.xmid
    this.bmgysid = this.$store.state.gyswdxm.bmgysid
    this.sfdlbb = this.$store.state.gyswdxm.sfdlbb
    this.xmmc = this.$store.state.gyswdxm.xmmc
    this.getGysLcxd()
  },
  beforeDestroy() {
    console.log('退出------------------------------------------------')
    this.$store.commit('gyswdxm/SET_GGXX', '')
    this.$store.commit('gyswdxm/SET_XMCQ', '')
    this.$store.commit('gyswdxm/SET_JGGS', '')
    this.$store.commit('gyswdxm/SET_JGGG', '')
    this.$store.commit('gyswdxm/SET_ZBJG', '')
    this.$store.commit('gyswdxm/SET_YQHQR', '')
  },
  methods: {
    getClass(status, index) {
      if (status === 1) {
        if (this.selectIndex === index) {
          return 'tab_item selected'
        } else {
          return 'tab_item'
        }
      }
      if (status === 2) {
        if (this.selectIndex === index) {
          return 'tab_item active selected'
        } else {
          return 'tab_item active'
        }
      }
      if (status === 3) {
        if (this.selectIndex === index) {
          return 'tab_item done selected'
        } else {
          return 'tab_item done'
        }
      }
    },
    getGysLcxd(name, id, status, index) {
      if (status === 1) {
        return
      }

      this.$store.dispatch('gyswdxm/getGysLcxdAction', { bizid: this.bmgysid, executorid: this.userInfo.id, taskId: id }).then(res => {
        this.phaseTaskList = res.phaseTaskList
        this.curDataTabList = res.curDataTabList
        this.appsrvurl = this.curDataTabList[0].appsrvurl
        if (this.name === '') {
          for (const i in this.phaseTaskList) {
            if (this.phaseTaskList[i].select === true) {
              this.name = this.phaseTaskList[i].name
              this.selectIndex = i - 1 + 1
            }
          }
        } else {
          this.name = name
        }
        console.log('name---------------', this.name)
        console.log('appsrvurl---------------', this.appsrvurl)
      }).catch((error) => {
        this.$toast(error)
      })
      this.selectIndex = index
    }
  }
}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none!important;}
.van-cell:not(:last-child)::after{display: none;}
</style>
