<template>
  <div>

    <div
      v-for="(item,index) in list"
      :key="index"
    >

      <mouldsList :data-item="item" :show-more="showMore" @goMoreAction="MoreAction($event)" />

    </div>

  </div>

</template>

<script land="script" scoped>
import { NativeJS } from '@/common/js/mixin.js'
// import mouldList from '@/components/list/mouldList'
import mouldsList from '@/components/list/mouldsList'

export default {
  name: 'Zbjg',
  components: {
    // mouldList,
    mouldsList
  },
  mixins: [NativeJS],
  props: {
    appsrvurl: {
      type: null,
      default: ''
    },
    xmid: {
      type: null,
      default: ''
    },
    sfdlbb: {
      type: null,
      default: ''
    }
  },
  data() {
    return {
      zzid: '',
      list: [],
      showMore: false
    }
  },
  computed: {

  },
  created() {
    this.zzid = this.$store.state.mh.mhUserInfo.zzid
    if (this.$store.state.gyswdxm.zbjg) {
      this.list = this.$store.state.gyswdxm.zbjg
    } else {
      this.getJggg()
    }
  },
  methods: {
    getJggg() {
      this.$store.dispatch('gyswdxm/getGysJdxqAction', { xmid: this.xmid, zzid: this.zzid, appsrvurl: this.appsrvurl }).then(res => {
        this.list = res
        this.$store.commit('gyswdxm/SET_ZBJG', res)
      })
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)
      this.NativeDownFiles(url)
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }
  }

}
</script>
