<template>
  <div>
    <div v-if="jdxxList && jdxxList.length>0" class="container-mini">
      <div class="content">
        <div
          v-for="(item,index) in jdxxList"
          :key="index"
        >

          <div v-if="item.ggnrStr">
            <div class="title title-page">
              <div class="title-left">
                <h2>公告信息</h2>
              </div>
            </div>

            <div class="common-main box">
              <div class="notice-content" v-html="item.ggnrStr" />
            </div>
          </div>

          <div v-else>

            <div class="title title-page">
              <div class="title-left">
                <h2>{{ item.title }}</h2>
              </div>
            </div>

            <div
              v-for="(items, indexs) in item.data"
              :key="indexs"
              class="common-main box"
            >
              <div
                v-for="(dxItems, dxIndexs) in items"
                :key="dxIndexs"
                class="page-list"
              >
                <div v-if="dxItems.name!=='公告id'" class="pagelist-left">{{ dxItems.name }}</div>
                <div v-if="dxItems.type === 1" class="pagelist-right">{{ dxItems.value }}</div>
                <div v-else-if="dxItems.type === 9 || dxItems.type === 10 || dxItems.type === 11 || dxItems.type === 12 " class="pagelist-right  ">
                  <div v-if="dxItems.files !== ''&& dxItems.files !== null ">
                    <ul
                      v-for="(fjItem, fjIndex) in dxItems.values"
                      :key="fjIndex"
                    >
                      <li @click="getFiles(dxItems.files[indexs])">
                        <i class="iconfont iconxiazai" />
                        {{ fjItem }}
                      </li>
                    </ul>
                  </div>
                </div>
                <!-- <div class="page-more noshadow" @click="goXmggnr(items)"> 查看公告内容 <i class="iconfont iconarrowright" /></div> -->

              </div>

              <!-- 更多 end -->
              <div v-if="item.totalpageno>1 && showMore" class="page-more" @click="goMore(item)">
                查看更多信息
                <i class="iconfont iconarrowright" />
              </div>
              <div class="page-more noshadow" @click="goXmggnr(items)"> 查看公告内容 <i class="iconfont iconarrowright" /></div>
            </div>

            <!-- 更多 end -->
            <div v-if="item.totalpageno>1 && showMore" class="page-more" @click="goMore(item)">
              查看更多信息
              <i class="iconfont iconarrowright" />
            </div>
          </div>

        </div>
      </div>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
  </div>

</template>

<script land="script" scoped>
import { NativeJS } from '@/common/js/mixin.js'
// import mouldList from '@/components/list/mouldList'
// import mouldsList from '@/components/list/mouldsList'
import { mapState } from 'vuex'
import errorPage from '@/components/result-page/index'
export default {
  name: 'One',
  components: {
    errorPage
    // mouldList,
    // mouldsList
  },
  mixins: [NativeJS],
  props: {
    appsrvurl: {
      type: null,
      default: ''
    },
    xmid: {
      type: null,
      default: ''
    },
    sfdlbb: {
      type: null,
      default: ''
    }
  },
  data() {
    return {
      pageStyles: 0,
      jdxxList: [],
      showMore: false
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    if (this.$store.state.gyswdxm.ggxx) {
      this.jdxxList = this.$store.state.gyswdxm.ggxx
    } else {
      this.getJdxx()
    }
  },
  methods: {
    parentReceive() {
      this.getJdxx()
    },
    goXmggnr(items) {
      this.$router.push({ name: 'Xmggnr', params: { ggid: items[0].value }})
    },
    getJdxx() {
      this.$store.dispatch('gyswdxm/getGysJdxqAction', { xmid: this.xmid, zzid: this.userInfo.zzid, appsrvurl: this.appsrvurl }).then(res => {
        if (res) {
          this.jdxxList = res
          this.pageStyles = 0
        } else {
          (
            this.pageStyles = 1
          )
        }
        this.$store.commit('gyswdxm/SET_GGXX', res)
      }).catch((error) => {
        this.pageStyles = 3
        this.$toast(error)
      })
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)
      this.NativeDownFiles(url)
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }
  }

}
</script>
<style lang="css" scoped>

</style>
