<template>
  <div>
    <div class="common-main box">
      <div v-if="dataItem.title" class="title title-page">
        <div class="title-left">
          <h2>{{ dataItem.title }}</h2>
        </div>
      </div>

      <div
        v-for="(items, indexs) in dataItem.data"
        :key="indexs"
        class="page-list"
      >
        <div v-if="items.type!==0" class="pagelist-left">{{ items.name }}</div>
        <div v-if="items.type === 1" class="pagelist-right"> {{ items.value }}</div>
        <div v-else-if="items.type === 2" class="pagelist-right">{{ getTime (items.value) }}</div>
        <div v-else-if="items.type === 9 || items.type === 10 || items.type === 11 || items.type === 12 " class="pagelist-right  blue">
          <div v-if="items.files !== ''&& items.files !== null ">
            <ul
              v-for="(fjItem, fjIndex) in items.values"
              :key="fjIndex"
            >
              <li @click="loadFiles(fjItem,items.files[fjIndex])">
                <i class="iconfont iconxiazai" />
                {{ fjItem }}
              </li>
            </ul>
          </div>
        </div>
      </div>

    </div>
    <!-- 更多 end -->
    <div v-if="dataItem.showmore && showMore" class="page-more" @click="goMore(index)">
      查看更多信息
      <i class="iconfont iconarrowright" />
    </div>

    <!-- <van-popup v-model="show" class="pdf-popup" closeable>
      <iframe :src="pdfSrc" border="0" style="width:100vw;height:100vh;" />
    </van-popup> -->
    <!-- <popFj :show-pop="showpop" :fj-url="fjUrl" :fj-name="fjName" /> -->
  </div>
</template>

<script>

// import popFj from '@/components/popup/popFj.vue'
import { NativeJS } from '@/common/js/mixin.js'
import { getUrl } from '@/common/js/catch'
import { formatTimes } from '@/common/js/util'

export default {
  components: {
    // popFj
  },
  mixins: [NativeJS],
  /**
     * 表单 {id:"",title:"",type:"",data[]}
     * type: 1-单表 2-列表
     * 字段{name:"",value:"",type:"",files:[],values:[]}
     * type:1-text 9-file 10-pdf 11-img 12-html
     *[{"id":null,"title":"采购方案信息","type":1,"data":[
         {"name":"项目名称","value":"zsj手机端测试","type":1,"file":null,"values":null,"files":null}
         ],"totalpageno":0,"showmore":null,"sqdDataList":[]}]
     */
  props: {
    dataItem: { // 数据源
      type: null,
      required: true
    },
    showMore: { // 是否展示更多
      type: null,
      required: true
    }
  },
  data() {
    return {
      showpop: false,
      fjUrl: '',
      fjName: ''
    }
  },
  computed: {

  },
  created() {
  },
  mounted() {

  },

  methods: {
    getTime(kbsj) {
      var time = formatTimes(kbsj)
      return time
    },
    getFiles(name, url) {
      this.showpop = true
      console.log('name===', name)
      console.log('url===', url)

      this.fjUrl = url
      this.fjName = name
    },
    goMore(item) {
      this.$emit('goMoreAction', item)
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)
      // 打开附件 10-PDF文件 11-图片文件 12-HTML文件
      // getFiles(type, url, value) {
      const last_len = fjItem.lastIndexOf('.')
      const len = fjItem.length
      const types = fjItem.substring(last_len, len)
      console.log('types=====', types)
      if (url.indexOf('http') === -1) {
        url = getUrl('VUE_APP_FILE_API') + url
      }
      console.log('url========', url)

      //   // pathf = url.substring(last_len, len)
      if (types === '.jpg' || types === '.png' || types === '.jpeg') { // image
        // this.$emit('go-image', url)
        this.SET_GO_IMG(url)
        this.$router.push({
          name: 'GgImages',
          params: { url: url }
        })
      } else if (types === '.html') { // html
        // this.$emit('go-html', url)
        this.$router.push({
          name: 'GgHtml',
          params: { url: url }
        })
      } else {
        this.NativeDownFiles(url)
      }
      // },
    }
  }
}
</script>

<style>

</style>
