<template>
  <div>
    <div v-if="list && list.length>0">
      <div
        v-for="(item, index) in list"
        :key="index"
      >
        <div v-if="item.ggnrStr || item.ggnrStr==='' ">
          <div class="common-info">
            <div class="common-main box">
              <div class="title title-page">
                <div class="title-left">
                  <h2>结果公告</h2>
                </div>
              </div>
              <div class="content" v-html="item.ggnrStr" />
            </div>
          </div>
        </div>

        <div v-else>
          <div class="common-info">
            <div class="title title-page">
              <div class="title-left">
                <h2>{{ item.title }}</h2>
              </div>
            </div>

            <div
              v-for="(items, indexs) in item.data"
              :key="indexs"
              class="common-main box"
            >
              <div
                v-for="(dxItems, dxIndexs) in items"
                :key="dxIndexs"
                class="page-list"
              >
                <div v-if="dxItems.name!=='公告id'" class="pagelist-left">{{ dxItems.name }}</div>
                <div v-if="dxItems.type === 1" class="pagelist-right">{{ dxItems.value }}</div>
                <div v-else-if="dxItems.type === 9 || dxItems.type === 10 || dxItems.type === 11 || dxItems.type === 12 " class="pagelist-right  ">
                  <div v-if="dxItems.files !== ''&& dxItems.files !== null ">
                    <ul
                      v-for="(fjItem, fjIndex) in dxItems.values"
                      :key="fjIndex"
                    >
                      <li @click="getFiles(dxItems.files[indexs])">
                        <i class="iconfont iconxiazai" />
                        {{ fjItem }}
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="page-more" @click="goXmggnr(items)"> 查看公告内容 <i class="iconfont iconarrowright" /></div>
            </div>

            <!-- 更多 end -->
            <div v-if="item.totalpageno>1 && showMore" class="page-more" @click="goMore(item)">
              查看更多信息
              <i class="iconfont iconarrowright" />
            </div>
          </div>

        </div>

      </div>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
  </div>
</template>

<script land="script" scoped>
import { NativeJS } from '@/common/js/mixin.js'
// import mouldList from '@/components/list/mouldList'
import errorPage from '@/components/result-page/index'

export default {
  name: 'XmJggg',
  components: {
    errorPage
    // mouldList,
  },
  mixins: [NativeJS],
  props: {
    appsrvurl: {
      type: null,
      default: ''
    },
    xmid: {
      type: null,
      default: ''
    },
    sfdlbb: {
      type: null,
      default: ''
    }
  },
  data() {
    return {
      pageStyles: 0,
      zzid: '',
      list: [],
      showMore: false
    }
  },
  computed: {

  },
  created() {
    this.zzid = this.$store.state.mh.mhUserInfo.zzid
    if (this.$store.state.gyswdxm.jggg) {
      this.list = this.$store.state.gyswdxm.jggg
    } else {
      this.getJggg()
    }
  },
  methods: {
    parentReceive() {
      this.getJggg()
    },
    goXmggnr(items) {
      this.$router.push({ name: 'Xmggnr', params: { ggid: items[0].value }})
    },
    getJggg() {
      this.$store.dispatch('gyswdxm/getGysJdxqAction', { xmid: this.xmid, zzid: this.zzid, appsrvurl: this.appsrvurl }).then(res => {
        if (res) {
          this.list = res
          this.pageStyles = 0
        } else {
          this.pageStyles = 1
        }
        this.$store.commit('gyswdxm/SET_JGGG', res)
      }).catch((error) => {
        this.pageStyles = 3
        this.$toast(error)
      })
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)
      this.NativeDownFiles(url)
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }
  }

}
</script>
<style lang="css" scoped>

</style>
