<template>
  <div>
    <div class="container-mini">
      <div class="content">
        <div
          v-for="(items, dataindexs) in jdxxList"
          :key="dataindexs"
        >
          <div v-if="items.title" class="title title-page">
            <div class="title-left">
              <h2>{{ items.title }}</h2>
            </div>
          </div>
          <div
            v-for="(item, index) in items.data"
            :key="index"
            class="common-main box"
          >
            <div
              v-for="(dxItems, dxIndexs) in item"
              :key="dxIndexs"
              :class="dxItems.zdlxxh ? 'page-list ' : 'page-list hide'"
            >
              <!-- sjlx 数据类型 1：数字型 3：字符型 12：图片型 15：附件型 20:pdf-->
              <!-- srfs 输入方式 1-输入 2-代码选择 3-公式计算 -->
              <!-- bz 标志 0-输入/文字 1-输入 -->
              <!-- sfbt 是否必填  1-必填 2-非必填-->
              <!-- sfkyxg 是否可以修改-->
              <div class="pagelist-left">{{ dxItems.name }}</div>
              <div>
                <div
                  v-if="
                    dxItems.sjlx === 12 ||
                      dxItems.sjlx === 15 ||
                      dxItems.sjlx === 20
                  "
                >
                  <XyUploadPlus
                    ref="uploadEl"
                    :is-show="false"
                    style="width: 100%"
                    :filed="dxItems.bzdmc"
                    :filegroup="dxItems.showvalue"
                  />
                </div>
                <div v-else class="pagelist-right">{{ dxItems.showvalue }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script land="script" scoped>
import { NativeJS } from '@/common/js/mixin.js'
import { mapState } from 'vuex'
import XyUploadPlus from '@/components/upload/uploadPlus'
export default {
  name: 'Jdxq',
  components: {
    XyUploadPlus
  },
  mixins: [NativeJS],
  props: {
    appsrvurl: {
      type: null,
      default: ''
    },
    xmid: {
      type: null,
      default: ''
    },
    sfdlbb: {
      type: null,
      default: ''
    }
  },
  data() {
    return {
      jdxxList: [],
      showMore: false
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.getJdxx()
  },
  methods: {
    getJdxx() {
      this.$store.dispatch('gyswdxm/getGysJdxqAction', { xmid: this.xmid, zzid: this.userInfo.zzid, appsrvurl: this.appsrvurl }).then(res => {
        this.jdxxList = res
      }).catch((error) => {
        this.$toast(error)
      })
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)
      this.NativeDownFiles(url)
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }
  }

}
</script>
<style lang="css" scoped>

</style>
