<template>
  <div v-if="pageStyles===0">
    <van-sticky :offset-top="50">
      <!-- 分包 begin -->
      <div class="scrolltab scrolltab-block">
        <ul ref="tabWrapper" class="tab_content">
          <!--<ul class="tab_content flex" ref="tabWrapper">-->
          <li v-for="(item,index) in cqList" :key="index" :class="getClass(index)">
            <span class="tit" @click="getCqxq(index)">第{{ index+1 }}次澄清</span>
          </li>
        </ul>
      </div>
    <!-- 分包 end -->
    </van-sticky>
    <div class="container-mini">
      <div v-for="(item,index) in cqxx" :key="index" class="content">
        <div class="common-main box">

          <div class="title title-page">
            <div class="title-left">
              <h2>{{ item.title }}</h2>
            </div>
          </div>

          <div v-for="(item1,index1) in item.data" :key="index1" class="page-list">
            <div class="pagelist-left">{{ item1.name }}</div>
            <div v-if="item1.name==='公告内容'" class="pagelist-right"><div v-html="item1.value" /></div>
            <div v-else-if="item1.value!==null" class="pagelist-right">{{ checkValue(item1.value) }}</div>
            <div v-for="(item2,index2) in item1.values" v-else :key="index2">
              <div class="pagelist-right" @click="loadFiles(item2,item1.files[0])"><i class="iconfont iconxiazai" />{{ item2 }}</div>
            </div>
          </div>
        </div>
        <div v-if="item.title==='数据变更' && sfdlbb===1" class="page-more" @click="goXmcqxx()"> 查看分包详情 <i class="iconfont iconarrowright" /></div>
      </div>
      <!-- 空白区域，撑高度 -->
      <div class="common-space" />
    </div>

  </div>
  <div v-else>
    <error-page
      :page-style="pageStyles"
      @errorButton="parentReceive"
    />
  </div>
</template>

<script land="script" scoped>
import { formatTimes } from '@/common/js/util'
import errorPage from '@/components/result-page/index'
export default {
  name: 'Xmcq',
  components: {
    errorPage
  },
  props: {
    xmid: {
      type: String,
      default: ''
    },
    sfdlbb: {
      type: null,
      default: ''
    }
  },
  data() {
    return {
      pageStyles: 0,
      cqxx: '',
      zzid: '',
      cqIndex: 0,
      cqList: []
    }
  },
  computed: {

  },
  created() {
    this.zzid = this.$store.state.mh.mhUserInfo.zzid
    if (this.$store.state.gyswdxm.xmcq) {
      this.cqList = this.$store.state.gyswdxm.xmcq
      if (this.$store.state.gyswdxm.cqxx) {
        this.cqxx = this.$store.state.gyswdxm.cqxx
      } else {
        this.getCqxq(this.cqList.length - 1)
      }
      this.cqIndex = this.cqList.length - 1
    } else {
      this.getXmcqList()
    }
  },
  methods: {
    checkValue(value) {
      var r = new RegExp('([0-9]{4})(0[1-9]|1[012])(0[1-9]|[12][0-9]|3[01])' +
                '([01][0-9]|2[0-3])[0-5][0-9]').test(value)
      if (r) {
        var time = formatTimes(value)
        return time
      } else {
        return value
      }
    },
    loadFiles(fjItem, url) {
      console.log('fjItem=====', fjItem)
      console.log('url=====', url)
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.downloadFile(url)
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('downloadFile', String(url), (data) => {
        })
      } else {
        this.$toast('开发环境不支持')
      }
    },
    parentReceive() {
      this.getXmcqList()
    },
    getTime(date) {
      var time = formatTimes(date)
      return time
    },
    getCqxq(index) {
      this.cqIndex = index
      this.$store.dispatch('gyswdxm/getGysCqxxAction', { cqid: this.cqList[index].id, zzid: this.zzid }).then(res => {
        this.cqxx = res
        console.log('cqxx--------', this.cqxx)
      })
    },
    getXmcqList() {
      this.$store.dispatch('gyswdxm/getGysXmcqAction', { xmid: this.xmid, zzid: this.zzid }).then(res => {
        console.log('res=====', res)
        if (res.length > 0) {
          this.cqList = res
          this.getCqxq(this.cqList.length - 1)
          this.cqIndex = this.cqList.length - 1
          this.$store.commit('gyswdxm/SET_XMCQ', res)
        } else {
          this.pageStyles = 1
        }
      })
    },
    change(index) {
      this.cqIndex = index
    },
    goXmcqxx() {
      this.$router.push({ name: 'XmCqxx', params: { xmid: this.xmid }})
    },
    getClass(index) {
      if (this.cqIndex === index) {
        return 'tab_item active'
      } else {
        return 'tab_item'
      }
    }
  }

}
</script>
<style lang="css" scoped>
.common-main{margin-top: 10px;}
.title-page + .common-main{margin-top:-10px;}
.van-sticky--fixed .scrolltab{margin-top: 110px;}
</style>
